@font-face {font-family: "iconfont";
    src: url('./iconfont/iconfont.eot?t=1506078457962'); /* IE9*/
    src: url('./iconfont/iconfont.eot?t=1506078457962#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
    url('./iconfont/iconfont.ttf?t=1506078457962') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
    url('./iconfont/iconfont.svg?t=1506078457962#iconfont') format('svg'); /* iOS 4.1- */
}

.iconfont {
    font-family:"iconfont" !important;
    font-style:normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.TodoItem {
    background-color: #2affb3;
    margin-top: 10px;
    position:relative;
    border-radius:5px;
    border:solid 1px #2affb3;
    padding:10px 30px;
    -webkit-transition: all 0.1s;
    -o-transition: all 0.1s;
    transition: all 0.1s;
    -webkit-box-shadow: 0px 9px 0px #319f98;
            box-shadow: 0px 9px 0px #319f98;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    opacity: 0.8;
}

.TodoItem > .title {
    font-size: 2rem;
    -ms-flex-positive: 1;
        flex-grow: 1;
    color: #000;
    padding: 0 .5em;
}

.TodoItem .check {
    width: 28px;
    height: 28px;
    position: relative;
    margin: 20px auto;
    background: #f8ff34;
    background: -webkit-gradient(linear, left top, left bottom, from(#fcfff4), color-stop(40%, #dfe5d7), to(#b3bead));
    background: -webkit-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    background: -o-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    background: linear-gradient(to bottom, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
    -webkit-box-shadow: inset 0px 1px 1px white;
            box-shadow: inset 0px 1px 1px white;
}
.TodoItem .check:checked + .title{
    text-decoration:line-through;
    color: #ccc;
}
.TodoItem .button {
   font-size: 2rem;
    cursor: pointer;
    color: #fff;
}
.TodoItem .button:hover{
    color: red;
}







